<script setup lang="ts">
import { ref, onMounted } from 'vue'
import LoaderIcon from '@/components/LoaderIcon.vue'
import CarResult from '@/components/CarResult.vue'
import { Car as CarData } from '@/types'
// @ts-ignore
import $ from 'jquery'
import CarService from '@/services/car-service'
import { getBookingNumber, getPaymentSession, getPaymentTransaction, getSessionPrefix } from '@/utils/common'
import router from '@/router'
import Payment from '@/composables/Payment'

const props = defineProps(['currentStep'])
const isLoading = ref<boolean>(true)
const hasError = ref<string | null>(null)
const prefix = getSessionPrefix()
const { retrieveUserSelectedCar } = CarService()
const car : CarData = retrieveUserSelectedCar()
const booking : string | null = getBookingNumber()
const transaction : string | null = getPaymentTransaction()
const session : string | null = getPaymentSession()
const search = localStorage.getItem(`${prefix}_user_search`)
const { authorizePayment  } = Payment()
const payload = ref<any>(null)
const responsee = ref<any>(null)

const toggleExtraOptions = () => {
   $('.extra-options').slideToggle()
}

onMounted(async () => {
   
   const response = await authorizePayment(session, booking, transaction)
   isLoading.value = false
   payload.value = JSON.stringify(response.data.payload)
   responsee.value = JSON.stringify(response.data.response)
})

</script>